<template>
  <navigation class="NavMonth" :order="2">
    <h1 slot="head">{{month[presentDateObject.month-1]}}</h1>
    <h2 slot="subHead">Month</h2>
    <div class="Contents" slot="contents">
      <div class="Contents_wrap">
        <div class="Contents_Head" v-for="(d,index) in days" :key="d+index">{{d}}</div>
        <div class="Contents_Dummy_before dayBox" v-for="day in beforeDays" :key="'before'+day"></div>
        <div
          class="Contents_Body dayBox"
          v-for="m in monthLength"
          :key="m"
          @click="focusCurrentDate(m)"
        >
          <img class="Img" :src="stringYearMonth(m)">
          <div class="Text">{{m}}</div>
        </div>
        <div class="Contents_Dummy_after dayBox" v-for="day in afterDays" :key="'after'+day"></div>
      </div>
    </div>
  </navigation>
</template>
<script>
export default {
  data() {
    return {
      days: ["Sun", "Mon", "Tue", "Wed", "Thr", "Fri", "Sat"],
      month: [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec"
      ]
    };
  },
  computed: {
    presentDateObject() {
      let date = this.$store.getters.getCurrentDate;
      return date;
    },
    stringYearMonth(day) {
      let date = this.$store.getters.getCurrentDate;
      return `https://frozen-reef-96493.herokuapp.com/post/${
        date.year
      }/${date.month - 1}/${day}`;
    },
    beforeDays() {
      return new Date(
        this.presentDateObject.year,
        this.presentDateObject.month - 1,
        1
      ).getDay();
    },
    afterDays() {
      return 35 - this.monthLength + this.beforeDays;
    },
    monthLength() {
      return (
        32 -
        new Date(
          this.presentDateObject.year,
          this.presentDateObject.month - 1,
          32
        ).getDate()
      );
    }
  },
  methods: {
    focusCurrentDate(date) {
      this.$store.commit("setCurrentDate", {
        year: this.presentDateObject.year,
        month: this.presentDateObject.month,
        date: date
      });
    }
  }
};
</script>
<style lang='scss' scoped>
.NavMonth {
  color: black;
}

.Contents {
  width: 876px;
  height: 100%;
  min-height: 600px;
  // background-color: gray;
  margin-left: auto;
  margin-right: 120px;
  &_wrap {
    padding-top: 2.5%;
    margin-left: auto;
    margin-right: 0;
    // background-color: mediumspringgreen;
    display: grid;
    grid-template-columns: repeat(7, 120px);
    grid-template-rows: 35px repeat(5, 120px);
    grid-gap: 2px;
  }
  &_Head {
    text-align: center;
    font-size: 20px;
    color: white;
  }
  &_Body {
    position: relative;
    color: white;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    > .Img {
      width: 100%;
      height: 100%;
    }
    > .Text {
      position: absolute;
    }
  }
}

.dayBox {
  border-radius: 100%;
  border: 0.1px solid white;
}
</style>



