import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters.js'
import mutations from './mutations.js'
import actions from './actions.js'

Vue.use(Vuex);

/** currentDate : {
 * type : object
 * data : {
 *      year: number,
        month: number,
        date: number}} 
*/
export default new Vuex.Store({
  state: {
    user: null,
    currentDate: null,
    records : null
  },
  getters,
  mutations,
  actions
})



